.title-project {
  color: var(--title-color);
  text-align: center;
}

.description-project {
  text-align: left;
}

.wrapper-video {
  min-width: 525px;
  max-width: 525px;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 30px;
}

.link {
  display: flex;
  gap: 5px;
  justify-content: center;
  align-items: center;
  transition: transform 0.3s ease-in-out;

  &:hover {
    transform: translateX(2.5px) translateY(-2.5px);
  }
}